@import "../../../core/App/App";

.service-card {
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: $border-radius-card !important;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 20px 30px !important;

  &-img {
    border-radius: $border-radius-card !important;
  }

  &-name {
    margin-left: 1rem !important;
    margin-top: .5rem !important;
  }

  &-descr {
    padding: .3rem .5rem;
    font-size: .98rem;

    &-title {
      color: rgba(0, 0, 0, 0.5);
      text-decoration: none;
    }
  }

  &-descr {
    margin: 1rem 0 !important;

  }

  &-btn {
    width: 100%;
    border-radius: $border-radius-card !important;

    &:hover {
      color: white;
    }
  }
}
