@import "../../App/App";
@keyframes swirl-in-fwd {
  0% {
    transform: rotate(-540deg) scale(0);
    opacity: 0;
  }
  100% {
    transform: rotate(0) scale(1);
    opacity: 1;
  }
}

.swirl-in-fwd {
  animation: swirl-in-fwd 0.3s ease-out both;
}
.Toastify__toast-theme--colored{
  margin-bottom: .5rem!important;
}

