@import "src/app/components/App/App";


.floating-label-custom {
  border-radius: $border-radius-card!important;
  &-icon {
    font-size: 1.2rem;
  }

  &-card {
    margin: 0 !important;
    padding: 0 !important;
    transform: none;

    &-form {
      height: 2.5rem;
    }

    &:hover {
      transform: none;
    }

    .floating-form-btn {
      display: flex;
      justify-content: right;
      align-items: inherit;
    }

    .form-control {
      border: none;


      &:focus {
        box-shadow: 0 0 0 0.25rem rgba(255, 255, 255, 0);
        border-color: rgba(181, 25, 24, 0);
      }

    }
  }
}






.image-filter {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #000;
  opacity: 0.6;
}
.floating{
  border-radius: $border-radius-card!important;
}
